.collectieNavigation{
    
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: row;
    flex-wrap:wrap;
    background: linear-gradient(to bottom right, #F49D1A, #EF9A53);
    min-height: 8rem;
    width: 100%;
}

.collectieNavigation__list{
    list-style: none;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    flex-wrap:wrap;
   
    gap: 2.5rem;
    padding: 2rem;
}

.collectieNavigation__listItem{
    color:white;
    font-size:3rem;
}

.collectieNavigation__listItem__Link{
    text-decoration: none;
    color: white;
    font-size: 2.5rem;
}

.collectieNavigation__buttonDelen{
    color:white;
    
}

@media screen and (max-width:400) {
    .collectieNavigation{
        background: linear-gradient(to bottom right, #F49D1A, #EF9A53);
        height: 10rem;
        width: 100%;
    }
    
    .collectieNavigation__list{
        list-style: none;
        display: flex;
        justify-content: center;
        align-items: center;
      
        text-align: center;
        float: right;
        gap: 1rem;
        padding: 2rem;
    }
    
    .collectieNavigation__listItem{
        color:white;
        font-size:3rem;
    }
    
    .collectieNavigation__listItem__Link{
        text-decoration: none;
        color: white;
        font-size: 2.5rem;
    }
    
    .collectieNavigation__buttonDelen{
        color:white;
        
    } 
}